<template>
	<section>
		<div class="inner">
	      <div class="crumb">
			当前位置：<a href="">首页</a><em>&gt;</em><span>平台声明</span>
		  </div>
	    </div>
		 <div class="inner">
	       <div class="cjwt clearfix">
	          <div class="cjwt-l">
	             <div id="firstpane" class="menu_list">
	             	<div v-for="(item,index) in data" class="helpNav">
	             		<p class="menu_head" @click="getIndex(index)" :class="index==titleNum ? 'current11' : ''">{{item.channelName}}</p>
					    <div class="menu_body" :class="index==titleNum ? 'currentActive' : ''" >
					      <a href="javascript:;" class='taba' v-for="(itemm,index) in item.infoList" @click="getActive(itemm.uuid,index)" :class="index == num ?'fontcolor':''">{{itemm.title}}</a>
					    </div>
	             	</div>
				</div>
	        </div>
	          <div class="htmltext" v-html="content.textHtml">
	            
	          </div>
	       </div>
   		</div>
	</section>
</template>


<script>
	import { helpData,listDataParticulars } from '../../api/info';
	
	export default{
		data(){
			return{
				data:[],
				content:'',
				num:0,
				uuid:'',
				titleNum:0,
				
			}
		},
		methods:{
			contentDate(){
				helpData({channelMDesc:'STAT'}).then(res=>{
					
					var dataArr = [];
					res.datas.forEach(o =>{
						dataArr.push(o)
					})
					this.data=dataArr
					var uuid = dataArr[this.titleNum].infoList[this.num].uuid
					var data ={
	                        "infoUuid":uuid
					 	}
					listDataParticulars(data).then(res =>{
						this.content=res.datas
					})
				})
			},
			getActive(id,ind){
				this.num = ind
				var data ={
                        "infoUuid":id
				 	}
				listDataParticulars(data).then(res =>{
					this.content=res.datas
				})
			},
			getIndex(index){
				
				this.titleNum = index
				this.num = 0
				helpData({channelMDesc:'STAT'}).then(res=>{
					var dataArr = [];
					res.datas.forEach(o =>{
						dataArr.push(o)
					})
					this.data=dataArr
					var uuid = dataArr[this.titleNum].infoList[this.num].uuid
					var data ={
	                        "infoUuid":uuid
					 	}
					listDataParticulars(data).then(res =>{
						this.content=res.datas
					})
				})
			},
			initData(){
				var name = decodeURIComponent(this.$own.getHref('id'));
				helpData({channelMDesc:'STAT'}).then(res=>{
					console.log(res)
					var dataArr = [];
					for(var i = 0;i<res.datas.length;i++){
						for(var j= 0;j<res.datas[i].infoList.length;j++){
							if(res.datas[i].infoList[j].title.indexOf(name)>-1){
								this.titleNum = i;
								this.num = j;
							}
						}
					}
					res.datas.forEach(o =>{
						dataArr.push(o)
					})
					this.data=dataArr
					var uuid = dataArr[this.titleNum].infoList[this.num].uuid
					var data ={
	                        "infoUuid":uuid
					 	}
					listDataParticulars(data).then(res =>{
						this.content=res.datas
					})
				})
			}
		},
		watch:{
			'$route' (to, from) {
				if(to.path == from.path){
					var name = this.$route.query.id
					this.initData(name)
				}
				
			}
		},
		created(){
		
		},
		mounted(){
			this.initData()
			this.contentDate()
		}
	}
</script>


<style>
	.currentActive{
		display: block !important;
	}
	.menu_body{
		display: none;
	}
	.fontcolor{
		color: #0066CC !important;
	}
	.htmltext{
	    width: calc(100% - 280px);
	    float: right;
	    padding: 20px;
	}
</style>


